import Vue from 'vue'

Vue.filter('round', function(value, decimals) {
    if (!value) {
        value = 0;
    }

    if (!decimals) {
        decimals = 0;
    }

    value = Math.round(value * Math.pow(10, decimals)) / Math.pow(10, decimals);
    return value;
});

Vue.filter('showThousandsInK', (value, decimals) => {
    if (value < 1000) {
        return value;
    }
    
    value = value / 1000;

    if (!decimals) {
        decimals = 0;
    }

    value = Math.round(value * Math.pow(10, decimals)) / Math.pow(10, decimals);
    return value + 'K';
});


Vue.filter('showInMinutesOrHours', function(value) {
    if (!value) {
        value = 0;
    }

    let finalMinutes = Math.round(value / 60);
    let finalHours = Math.round(finalMinutes / 60);

    if (finalMinutes === 0) finalMinutes = 1;

    let readingTimeInNativeLanguage;
    if (finalHours > 1) {
        readingTimeInNativeLanguage = finalHours + ' ' + "மணி நேரம்";
    } else if (finalHours === 1) {
        readingTimeInNativeLanguage = finalHours + ' ' + "மணி நேரம்";
    } else if (finalMinutes > 1) {
        readingTimeInNativeLanguage = finalMinutes + ' ' + "நிமிடங்கள்";
    } else {
        readingTimeInNativeLanguage = finalMinutes + ' ' + "நிமிடம்";
    }

    return readingTimeInNativeLanguage;
});

Vue.filter('getPratilipiTypeInNativeLanguage', function(value) {
    switch (value) {
        case 'STORY':
            return "கதை";
        case 'POEM':
            return "கவிதை";
        case 'ARTICLE':
            return "கட்டுரை";
        case 'BOOK':
            return "புத்தகம்";
        case 'MAGAZINE':
            return "இதழ்";
    }
});

Vue.filter('getTranslatedLoginErrorMessage', function(value) {
    console.log(value);
    switch (value) {
        case 'Email not registered.':
            return "இம்மின்னஞ்சல் முகவரி இதுவரை எங்களிடம் பதிவு செய்யப்படவில்லை";
        case 'Invalid Credentials!':
            return "உள்நுழைதல் தோல்வியடைந்தது. மீண்டும் முயற்சிக்கவும்.";
        case 'The email address is already registered':
            return "மின்னஞ்சல் முகவரி முன்னரே பதிவு செய்யப்பட்டுள்ளது";
        default:
            return value;
    }
});

Vue.filter('convertDate', function(value) {
    const d = new Date(value);

    function day(d) { return (d < 10) ? '0' + d : d; }

    function month(m) {
        var months = [
            "ஜனவரி", 
            "பிப்ரவரி", 
            "மார்ச்",
            "ஏப்ரல்", 
            "மே", 
            "ஜூன்",
            "ஜூலை", 
            "ஆகஸ்ட்", 
            "செப்டம்பர்",
            "அக்டோபர்", 
            "நவம்பர்", 
            "டிசம்பர்"
        ];
        return months[m];
    }
    return [day(d.getDate()), month(d.getMonth()), d.getFullYear()].join(' ');
});



